@import '../../style/variables';

.content__public-offer {
  margin: 30px 0 45px 0;
  padding: 0 20px;
}

.public-offer {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: $main-color-text;
  &__wrapper {
    display: grid;
    grid-template: 1fr / minmax(auto, 960px);
  }
}